body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:grey;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  padding-top: 5rem;
  margin: 0 auto;
  width: 600px;
  padding-right: 1rem;
  padding-left: 1rem;
}

.todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: .5rem;
}

.todo-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.todo-form input {
  display: inline-block;
  width: 85%;
}

.todo-check {
  margin-right: 1rem;
}

.rm {
  background: rgb(211, 121, 121);
  border-radius: 20%;
  color: #fff;
  border: none;
}

.done {
  text-decoration: line-through;
}

.remaining {
  margin: 0;
  margin-right: auto;
  font-size: 1rem;
}

.filters-wrapper {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  border-top: #fff solid 1px;
  padding-top: .5rem;
  margin-top: 1rem;
}